import React, { useState } from 'react'
import { ReactComponent as Like } from './icon/Like.svg'
import { ReactComponent as MoreSetting } from './icon/MoreSetting.svg'
import { questionToggleLike } from '../../../../actions/question'
import moment from 'moment'
import './style.css'

moment.locale('ru', {
    months : '___________'.split('_'),
    monthsShort : '._.__.____._._._._.'.split('_'),
    relativeTime : {
        future : ' %s',
        past : '%s ',
        s : ' ',
        m : '1 ',
        mm : '%d ()',
        h : '1 ',
        hh : '%d ()',
        d : '1 ',
        dd : '%d ()',
        M : '1 ',
        MM : '%d ()',
        y : ' ',
        yy : '%d ()'
    }
});
export const QuestionItem = ({ data, users = {}, isProfile }) => {
    const [isLiked, setIsLiked] = useState(data?.isLiked)
    const [likes, setLikes] = useState(data?.likes)
    const userId = data.userId

    let firstName, lastName, avatar;
    users.forEach(user => {
        if (user?.id !== undefined && user?.id === userId) {
            firstName = user?.firstName !== undefined ? user?.firstName : '';
            lastName = user?.lastName !== undefined ? user?.lastName : '';
            const photo = user?.photo !== undefined ? user?.photo : ''

            avatar = {
                backgroundImage: 'url(' + photo + ')',
                backgroundSize: '48px 48px',
            }
        }
    });

    const answerDateTime = () => {
        const answerAt = data?.answeredAt * 1000 || 0;
        const date = new Date(answerAt);

        if (new Date().getMonth() - date.getMonth() > 0) {
            return moment(date).format("LLL")
        }

        return moment(date).fromNow()
    }

    const answerAt = answerDateTime()
    const newLocal = (likes || likes !== 0) && <span>{likes}</span>
    return (
        <div className="me-qestion-out-container-reply">
            <div className="me-qestion-out-text">
                {!isProfile ? (
                        <div className='me-question-out-reply-info'>
                            <div className='me-question-out-info-user-photo' style={avatar}></div>
                            <div className='me-question-out-info-user'>
                                <p>{firstName} {lastName}</p>
                                <span>{answerAt}</span>
                            </div>
                        </div>
                    ) : (
                        <span>{answerAt}</span>
                    )
                }
                <div className="me-qesrion-out-text-reply">
                    <p>{data.text}</p>
                    <span>{data.answer}</span>
                </div>
            </div>
            <div className="me-question-out-action">
                <div 
                    className={"me-question-out-reply " + (!isLiked ? "like" : "unlike")}
                    onClick={() => questionToggleLike(data?.id, isLiked, setIsLiked, setLikes)}
                >
                    <Like />
                    {newLocal}
                </div>
                {/* <div className='me-question-out-reply-setting'>
                    <MoreSetting />
                </div> */}
            </div>
        </div>
    )
}